@import '../../styles/shared.scss';

.search-city {
  margin-top: 1rem;
}
.header {
  padding-inline: 1.5rem;
  display: flex;
  justify-content: center;
}
.header__left {
  width: 55%;
  display: inline-block;
}
.header__right {
  width: 45%;
  display: inline-block;
  text-align: right;
  justify-content: center;
}
.above-block {
  @extend .text--big;
  padding-left: 1.5rem;
  margin: 1rem 0 0.5rem 0;
}
.day-forecast__block {
  @extend .block;
  overflow-x: scroll;

  &::-webkit-scrollbar {
    overflow-x: scroll;
    height: 10px;
    background-color: rgba(0, 0, 0, 0);
  }
  &::-webkit-scrollbar-track {
    padding: 0 20px;
    margin: 0 20px;
  }
  &::-webkit-scrollbar-thumb {
    background-color: white;
    height: 5px;
    border-radius: 10px;
  }
  .day-forecast__pack {
    display: flex;
  }
  .day-forecast__subblock {
    display: inline-block;
    width: 6rem;

    p {
    text-align: center;
    }
    img {
    display: block;
    margin: 0 auto;
    @extend .icon--medium
    }
  }
  .day-forecast__separator {
    display: flex;
    width: 2.5rem;

    .day-forecast__separator__line{
      display: inline-block;
      width: 0px;
      height: 100%;
      border: 2px solid transparent;
      border-image: linear-gradient(to top, rgba(white, 0%), white); /* Apply a linear gradient as the border image */
      border-image-slice: 1; 
    }
    .day-forecast__separator__date{
      display: inline-block;
      margin: 0.3rem;
      writing-mode: vertical-lr;
    }
  }
}

.add-info-weather {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 0.5rem;

  p{
    margin-block: 0.2rem;
  }

  .add-info-weather__block--small {
    @extend .block;
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    padding: 1.5rem;
    align-items: center;
  }
  .add-info-weather__block--half {
    grid-area: 1 / 3 / span 2 / span 2;
    display: flex;
    justify-content: center;
    @extend .block;
  }
}

.under-sun-direct__div {
  display: flex;
  justify-content: space-between;
}

.add-info-weather__day-light-hours {
  height: 100%;
  width: 28rem;
}

.sun-direct__svg circle{
  animation-name: rotateAnimation; /* Name of the animation */
  animation-duration: 60s; /* Duration of the animation */
  animation-timing-function: linear; /* Timing function for the animation */
  animation-iteration-count: infinite; /* Number of times the animation repeats */
}

@keyframes rotateAnimation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}